import { getSymbolsInSelection } from '../';

const pushCursorToStack = (
  valueStack,
  key,
  selectionStart,
  selectionEnd,
  pattern,
  pastedLength = null,
  cursorChar = '|',
) => {
  const newStack = [...valueStack];
  let cursorPosition = selectionStart - getSymbolsInSelection(0, selectionStart, pattern);

  switch (key) {
    case 'Cut':
    case 'Delete':
      break;

    case 'Backspace':
      // If not multiple delete.
      if (selectionStart === selectionEnd) {
        cursorPosition = cursorPosition > 0 ? cursorPosition - 1 : 0;
      }
      break;

    case 'Paste':
      cursorPosition += pastedLength;
      break;

    default:
      cursorPosition += 1;
      break;
  }
  newStack.splice(cursorPosition, 0, cursorChar);

  return newStack;
};

export default pushCursorToStack;



