/*eslint-disable*/
const unformatWithPattern = (value, pattern) =>
  value && value.length
    ? value
        .toString()
        .split('')
        .filter(
          el =>
            pattern
              .split('')
              .filter(symbol => symbol !== '*')
              .indexOf(el) === -1,
        )
    : [];

export default unformatWithPattern;



