/*eslint-disable */
import getPatternSymbolsWithPosition from '../getPatternSymbolsWithPosition';

const getNextGroupedSymbols = (selectionStart, pattern, direction = 'default') => {
  const patternArray = getPatternSymbolsWithPosition(pattern);
  let cursor = selectionStart;
  let groupSize;

  if (direction === 'default') {
    const applicablePattern = patternArray.filter(symbol => symbol.index >= selectionStart);
    for (groupSize = 0; groupSize < applicablePattern.length; groupSize++) {
      if (applicablePattern[groupSize].index === cursor) {
        cursor += 1;
      } else {
        // Exit if not subsequent.
        break;
      }
    }
  } else {
    const applicablePattern = patternArray
      .filter(symbol => symbol.index < selectionStart)
      .reverse();
    for (groupSize = 0; groupSize < applicablePattern.length; groupSize++) {
      if (applicablePattern[groupSize].index === cursor - 1) {
        cursor -= 1;
      } else {
        // Exit if not subsequent.
        break;
      }
    }
  }

  return groupSize;
};

export default getNextGroupedSymbols;



